div[class^="home--"] {
    width: 100%;
    height: 100%;
    overflow: auto;
}

.home--light-mode {
    background: white;

    .item {
        background-color: white;
    }

    .theme-selector__select {
        background: white;
        color: black;
    }
}

.home--dark-mode {
    background: #131516;
    color: white;

    .item {
        background-color: #131516;
        border: 0.0625rem solid #8C8273;
    }

    .item__text {
        color: white;
    }

    .theme-selector__select {
        background: #131516;
        border: 0.0625rem solid white;
        color: white;
    }
}
